import React, { Component } from 'react';
import { Form, Input, Icon, Button, Progress, message } from 'antd';
import { Link } from 'react-router-dom';
import { _losePassword_step1, _losePassword_step2, _losePassword_step3 } from '../../component/api'; //ajax
import GetCode from '../../component/getCode';            //
const publics = require('../../component/publics'); //

class FindPasswords extends Component {
  constructor() {
    super();
    this.state = {
      pwdState: 1,                          //
    }
  }

  // 
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  // 
  handleSubmit1 = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if ( !publics.phoneNum.test(values.mobile) ) {
          message.error("")
        } else {
          _losePassword_step1(values)
          .then(res => {
            // console.log(res.data);
            if ( res.data.code === 200 ) {
              this.setState({
                hide_mobile: res.data.data.hide_mobile,                 //
                pwdState: 2,                                            //pwdState 
                phoneNumbern: res.data.data.mobile,                     //
                newAdmin: 2,                                            //2 
              })
              message.success(res.data.msg)
            } else {
              message.warning(res.data.msg)
            }
          })
          .catch(err => {
            console.log(err);
          })
        }
      }
    });
  }

  //  input
  vCode = (e) => {
    this.setState({
      code: e.target.value
    })
  }
  // 
  handleSubmit2 = (e) => {
    e.preventDefault(e);
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let datas = {
          mobile: this.state.phoneNumbern,
          code: this.state.code,
        }
        _losePassword_step2(datas)
        .then(res => {
          // console.log(res.data);
          if ( res.data.code === 200 ) {
            this.setState({
              pwdState: 3,                           //pwdState                                    //pwdState 
              pri_key: res.data.data.pri_key,        //id
            })
          } else {
            message.warning(res.data.msg)
          }
        })
        .catch(err => {
          console.log(err);
        })
      }
    });
  }

  // 
  handleSubmit3 = (e) => {
    e.preventDefault(e);
    this.props.form.validateFields((err, values) => {
      if (!err) {
        values.pri_key = this.state.pri_key;
        _losePassword_step3(values)
        .then(res => {
          // console.log(res.data);
          if ( res.data.code === 200 ) {
            message.success(res.data.msg);
            this.props.history.push('/');
          } else {
            message.warning(res.data.msg)
          }
        })
        .catch(err => {
          console.log(err);
        })
      }
    });
  }

  render(){
    const { pwdState, hide_mobile, phoneNumbern, newAdmin } = this.state;
    const { getFieldDecorator } = this.props.form;
    return(
      <div className="login">
        {/* header */}
        <div className="login-header">
          <div className="login-header-left">
            <img src={require("../../imgs/logo.png")} alt="logo"/>
            <span></span>
          </div>
          <div className="login-header-right">
            <Link to="/"></Link>
            or
            <Link to="/register"></Link>
          </div>
        </div>
        {/* content */}
        <div className="login-content">
          {/*  */}
          <div className="content-left">
            {/* <img src={require("../../imgs/logo.png")} alt="logo"/> */}
          </div>
          {/*  */}
          <div className="content-right" style={{ textAlign: 'center' }}>
            <Progress type="circle" width={90} percent={ pwdState === 1 ? 25 : (pwdState === 2 ? 50 : 100) } />
            {
              pwdState === 1 ?
                <p style={{ padding: '19px 0', fontSize: '18px' }}></p>
              :
              (pwdState === 2 ?
                <p style={{ padding: '19px 0', fontSize: '18px' }}></p>
              :
              <p style={{ padding: '19px 0', fontSize: '18px' }}></p>
              )
            }
            {/*   */}
            {
              pwdState === 1 ?
                <Form onSubmit={this.handleSubmit1} className="login-form loginPage">
                  <Form.Item style={{ marginRight: '0' }}>
                    {getFieldDecorator('mobile', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input maxLength={11} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="\" />
                    )}
                  </Form.Item>
                  <Form.Item style={{ marginRight: '0' }}>
                    <Button type="primary"  htmlType="submit" style={{ width: '100%' }}></Button>
                  </Form.Item>
                </Form>
              :
              (pwdState === 2 ?
                <Form onSubmit={this.handleSubmit2} className="login-form loginPage">
                  <Form.Item style={{ marginRight: '0', marginBottom: '0' }}>
                    <p>{hide_mobile}</p>
                  </Form.Item>
                  <Form.Item style={{ marginRight: '0' }}>
                    {/* phoneNumbern newAdmin  */}
                    <GetCode vCode={this.vCode} phoneNumbern={phoneNumbern} newAdmin={newAdmin} />
                  </Form.Item>
                  <Form.Item style={{ marginRight: '0' }}>
                    <Button type="primary"  htmlType="submit" style={{ width: '100%' }}></Button>
                  </Form.Item>
                </Form>
              :
              <Form onSubmit={this.handleSubmit3} className="login-form loginPage">
                <Form.Item
                  label=""
                  style={{ marginRight: '0', display: 'flex', justifyContent: 'space-around' }}
                >
                  {getFieldDecorator('pwd', {
                    rules: [{ required: true, message: '!' }],
                  })(
                    <Input type="password" prefix={<Icon type="lock" />} placeholder="" />
                  )}
                </Form.Item>
                <Form.Item
                  label=""
                  style={{ marginRight: '0', display: 'flex' }}
                >
                  {getFieldDecorator('confirm_pwd', {
                    rules: [{ required: true, message: '!' }],
                  })(
                    <Input type="password" prefix={<Icon type="lock" />} placeholder="" />
                  )}
                </Form.Item>
                <Form.Item style={{ marginRight: '0' }}>
                  <Button type="primary"  htmlType="submit" style={{ width: '100%' }}></Button>
                </Form.Item>
              </Form>
              )
            }
          </div>
        </div>
      </div>
    )
  }
}

const FindPassword = Form.create()(FindPasswords);
export default FindPassword
