import React, { Component } from 'react';
import { Form, Select, Input, Button, Cascader, message } from 'antd';
import { _addstore, _getCategory, _storecheckcode } from '../../../component/api'; //ajax
import copy from 'copy-to-clipboard';
const city_new = require('../../../component/city.js');    //
const publics = require('../../../component/publics');        //

const options = city_new.data.RECORDS;    //
const Option = Select.Option;

class BindFroms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      category_oneShow: false,                                        //yijileimu 
      category_twoShow: false,                                        //erjileimu 
    }
    // console.log(props);
  }

  componentWillMount() {
    // 
    _getCategory()
    .then(res => {
      this.setState({
        category_one: res.data.data,
        category_oneShow: true,
      })
    })
    .catch(err => {
      console.log(err);
    })
    // shop
    _storecheckcode()
    .then(res => {
      // console.log(res.data);
      this.setState({
        code: res.data.data.code
      })
    })
  }

  // closeState add Shop from
  closeState = () => {
    this.props.hidden()
  }
  // category_one 
  handleChange = (e) => {
    // console.log(e);
    let categoryData = {
      id: e
    }
    // category_two ajax
    _getCategory(categoryData)
    .then(res => {
      this.setState({
        category_two: res.data.data,
        category_twoShow: true,
      })
    })
    .catch(err => {
      console.log(err);
    })
  }

  // shop code
  copyBtn = () => {
    copy(this.state.code);                              //
    message.success('');
  }
  // 
  // onChange = (value) => {
  //   console.log(value);
  // }

  // from
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        if ( !publics.phoneNum.test(values.send_phone) ) {
          message.error("");
        } else {
          _addstore(values)
          .then(res => {
            console.log(res.data);
            message.warning(res.data.msg);
          })
          .catch(err => {
            console.log(err);
          })
        }
      }
    });
  }

  render() {
    const { category_one, category_two, category_oneShow, category_twoShow, code } = this.state;
    const { getFieldDecorator } = this.props.form;
    return(
      <div className="bind_box">
        <div className="new_header">
          <h2>bind new shop</h2>
          <span>jjio Dojodo // DEBUG: </span>
        </div>
        <Form className="addGoods_form new_headerChild" onSubmit={this.handleSubmit}>
          <Form.Item
            label="Gender"
          >
            {getFieldDecorator('category_one', {
              rules: [{ required: true, message: 'Please select your gender!' }],
            })(
              <Select placeholder="" style={{ width: 180 }} onChange={this.handleChange}>
                {
                  category_oneShow ?
                    category_one.map((item,index) => {
                      return(
                        <Option key={item.id} value={item.id}>{item.name}</Option>
                      )
                    })
                  :
                  ''
                }
              </Select>
            )}
          </Form.Item>
          <Form.Item
            label="Gender"
          >
            {getFieldDecorator('category_two', {
              rules: [{ required: true, message: 'Please select your gender!' }],
            })(
              <Select placeholder="" style={{ width: 250 }}>
                {
                  category_twoShow ?
                    category_two.map((item,index) => {
                      return(
                        <Option key={item.id} value={item.id}>{item.name}</Option>
                      )
                    })
                  :
                  ''
                }
              </Select>
            )}
          </Form.Item>
          {/* <Form.Item
            label="home URL"
            className="form_item"
            >
            {getFieldDecorator('note', {
              rules: [{ required: true, message: 'Please input your note!' }],
            })(
              <Input placeholder="shopsLink" />
            )}
          </Form.Item> */}
          <div className="new_header form_divs">
            <div></div>
            <p>{code}</p>
            <Button onClick={this.copyBtn} type="primary">Copy</Button>
          </div>
          <div className="form_divs" style={{ display: 'block' }}>
            <div>1.</div>
            <img src={require('../../../imgs/captchaImg.png')} alt="typeShop"/>
          </div>
          <div className="form_divs" style={{ display: 'block' }}>
            <div>2.<span style={{ color: '#FF6000' }}>jijidsojgojewjgfksldsfdsljfslij</span></div>
          </div>
          <Form.Item
            label="goods URL"
            className="form_item"
          >
            {getFieldDecorator('goods_url', {
              rules: [{ required: true, message: 'Please input your babyName!' }],
            })(
              <Input placeholder="shopsLink" />
            )}
          </Form.Item>
          {/* <Form.Item
            label="wangwang"
            className="form_item"
            >
            {getFieldDecorator('wangwang', {
              rules: [{ required: true, message: 'Please input your babyName!' }],
            })(
              <Input placeholder="shopsLink" />
            )}
          </Form.Item> */}
          {/* <Form.Item
            label="shopName"
            className="form_item"
            >
            {getFieldDecorator('babyName', {
              rules: [{ required: true, message: 'Please input your babyName!' }],
            })(
              <Input placeholder="shopsLink" />
            )}
          </Form.Item> */}
          <div>
            <Form.Item
              label=""
              className="form_item"
              style={{ display: 'flex', width: '30%' }}
            >
              {getFieldDecorator('site', {
                rules: [{ required: true, message: '!' }],
              })(
                <Cascader style={{ width: '100%', marginLeft: '10px' }} options={options} onChange={this.onChange} placeholder="" />
                // {/* <Input placeholder="shopsLink" /> */}
              )}
            </Form.Item>
            <Form.Item
              style={{ width: '50%' }}
            >
              {getFieldDecorator('address', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input style={{ marginLeft: '20px' }} placeholder="" />
              )}
            </Form.Item>
          </div>
          <Form.Item
            label="send_name"
            className="form_item"
          >
            {getFieldDecorator('send_name', {
              rules: [{ required: true, message: 'Please input your send_name!' }],
            })(
              <Input placeholder="shopsLink" />
            )}
          </Form.Item>
          <Form.Item
            label="send_phone "
            className="form_item"
          >
            {getFieldDecorator('send_phone', {
              rules: [{ required: true, message: 'Please input your send_phone!' }],
            })(
              <Input placeholder="shopsLink" />
            )}
          </Form.Item>
          {/*  */}
          <Form.Item className="submit_btn">
            <Button onClick={this.closeState} className="bottom_btns" type="danger" ghost>danger</Button>
            <Button className="bottom_btns" type="primary" htmlType="submit">Submit</Button>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

const BindFrom = Form.create()(BindFroms);
export default BindFrom
