import React, { Component } from 'react';
import { Form, Input ,Button, Icon, message } from 'antd';
import { Link } from 'react-router-dom';
import GetCode from '../../component/getCode';            //
import { _register } from '../../component/api';
import '../../component/api';
import "../login/login.css";
const publics = require('../../component/publics'); //
const phoneNum = publics.phoneNum; //
const pawNum = publics.pawNum;     //

class Registers extends Component {
  constructor() {
    super();
    this.state = {
      code: null,
    }
  }

  componentWillMount() {

  }

  // 
  phoneNumber = (e) => {
    this.setState({
      phoneNumbern: e.target.value
    })
  }
  //  
  vCode = (e) => {
    this.setState({
      code: e.target.value
    })
  }

  // 
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        values.code = this.state.code;              //  values
        // console.log(values);
        if ( !phoneNum.test(values.mobile) ) {
          message.error("")
        } else if ( this.state.code === null ) {
          message.error("")
        } else if ( !pawNum.test(values.pwd) ) {
          message.error("6")
        } else {
          // form  ajax
          _register(values)
          .then(res => {
            // console.log(res.data)
            if ( res.data.code === 200 ) {
              message.success(res.data.msg);
              this.props.history.push('/');
            } else {
              message.warning(res.data.msg)
            }
          })
          .catch( err => {
            console.log(err);
          })

        }
      }
    });
  }

  render() {
    const { phoneNumbern, } = this.state;
    const { getFieldDecorator } = this.props.form;
    return(
      <div className="login">
        {/* header */}
        <div className="login-header">
          <div className="login-header-left">
            <img src={require("../../imgs/logo.png")} alt="logo"/>
            <span></span>
          </div>
          <div className="login-header-right">
            <span></span>
            <Link to="/"></Link>
          </div>
        </div>
        {/* content */}
        <div className="register-content">
          {/*  */}
          <div className="register-left">
            {/* <h1></h1>
              <p>1.</p>
              <p>2.(1-20)</p>
              <p>3.(6)</p>
            <p>4.</p> */}
            <h1></h1>
            <p>1.</p>
            <p>2.</p>
            <p>3.</p>
            <p>4.</p>
            <img src={require("../../imgs/registerImg.jpg")} alt=""/>
          </div>
          {/*  */}
          <div className="register-right">
            {/* <h1></h1> */}
            <h1></h1>
            <Form onSubmit={this.handleSubmit} className="login-form loginPage">
              <Form.Item>
                {getFieldDecorator('reg_code', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input prefix={<Icon type="qrcode" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('mobile', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input onChange={this.phoneNumber} prefix={<Icon type="phone" style={{ color: 'rgba(0,0,0,.25)' }} />} maxLength={11} placeholder="" />
                )}
              </Form.Item>
              {/* <Form.Item>
                {getFieldDecorator('tuCode', {
                  rules: [{required: true,message: '!'}]
                })(
                  <Row gutter={16}>
                <Col span={12}>
                <Input className="register-input" prefix={<Icon type="picture" style={{ color: 'rgba(0,0,0,.25)' }} />} onChange={ this.tuCodes } placeholder=""/>
                </Col>
                <Col span={12}>
                <img style={{ width: "100%" }} src={ require("../../imgs/captchaImg.png") } alt=""/>
                </Col>
                  </Row>
                )}
              </Form.Item> */}
              <Form.Item>
                <GetCode vCode={this.vCode} phoneNumbern={phoneNumbern}  />
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('pwd', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="6-16" />
                )}
              </Form.Item>
              <Form.Item style={{ marginRight: '30px' }}>
                {getFieldDecorator('qq', {
                  rules: [{ required: true, message: 'QQ!' }],
                })(
                  <Input prefix={<Icon type="qq" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="QQ" />
                )}
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit" className="login-form-button">
                  
                </Button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    )
  }
}

const Register = Form.create()(Registers);
export default Register
