// goods
import React, { Component } from 'react';
import { Breadcrumb, Icon, Button, Tabs, Radio, Modal, Input, Select, Pagination, message } from 'antd';
import { Link } from 'react-router-dom';

import PageHeader from '../../../component/page_header/page_header';         //
import AddGoodComponent from '../addGoods/addGoodComponent';                 //
import Type1 from './type1';                                                //taobao
import Type2 from './type2';                                                //taobao
import KeywordComponent from './keyword/keyword';                             //KeywordComponent
import AppreciationServe from './appreciation/appreciationServe';            //
import { _Publishindex, _GoodsInfoList, _keyWayList, _PublishTBSearch } from '../../../component/api';                     //ajax

import './release.css';

const TabPane = Tabs.TabPane;                   //Tabs
const RadioGroup = Radio.Group;                 //
const Option = Select.Option;                   //

class ReleaseTask extends Component  {
  constructor() {
    super();
    this.state = {
      activeKey: '1',                             //
      spell: 100,                                 // 
      visible: false,                             //visible true   
      addGoodsNum: true,                          // addGoodsNum true
      commodity_div: false,                       // commodity_div true 
      commodity1: false,                          //false  
      additionalReview: false,                    // 
      publish_type: false,                        //  true
      recentlyShow: true,                         //
      fanganShow: true,                          // 

    }
  }

  componentWillMount() {
    // 
    _Publishindex()
    .then(res => {
      // console.log(res.data.data);
      this.setState({
        Ecommerce_type: res.data.data,                    //
      })
    })
    .catch(err => {
      console.log(err);
    });
    // 
    let platform = {  // 
      platform: this.state.spell,
    }
    _GoodsInfoList(platform, 1)
    .then(res => {
      // console.log(res.data.data);
      this.setState({
        goodsLists: res.data.data.list,                                         //
        page_count: res.data.data.page_count,                                   //
        total: res.data.data.total,                                             //
      })
    })
    .catch(err => {
      console.log(err);
    })
  }
  // 
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }
  
  // task
  onTabs = (key) => {
    // console.log(key);
    if ( key === '1' ) {
      this.setState({
        recentlyShow: true,
      })
    } else {
      this.setState({
        recentlyShow: false,
      })
    }
  }

  //  pingtai
  onSpell = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      spell: e.target.value,
      commodity_div: false,
      commodity1: false,
      addGoodsNum: true,
      fanganShow: false,
    });
    // 
    let platform = {  // 
      platform: e.target.value,
    }
    _GoodsInfoList(platform, 1)
    .then(res => {
      // console.log(res.data.data);
      this.setState({
        goodsLists: res.data.data.list,                                         //
        page_count: res.data.data.page_count,                                   //
        total: res.data.data.total,                                             //
      })
    })
    .catch(err => {
      console.log(err);
    })
  }

  FudianBtn = (e) => {
    console.log(e.target.value);
    if ( e.target.value === 201 ) {
      this.setState({
        additionalReview: true,
        commodity_div: false,
        publish_type: true,
        commodity1: false,
      })
    } else if ( e.target.value === 202 ) {
      this.setState({
        publish_type: true,
        additionalReview: false,
      })
    } else {
      this.setState({
        additionalReview: false,
        publish_type: false,
        commodity_div: false,
        commodity1: false,
      })
    }
  }

  // 
  xuanzeBtn = () => {
    this.setState({
      visible: true,
    });
  }
  // 
  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  }
  // 
  onPagination = (pageNumber) => {
    // 
    let platform = {  // 
      platform: this.state.spell,
    }
    // 
    _GoodsInfoList(platform, pageNumber)
    .then(res => {
      // console.log(res.data.data);
      this.setState({
        goodsLists: res.data.data.list,                                         //
      })
    })
    .catch(err => {
      console.log(err);
    })
    // console.log('Page: ', pageNumber);
  }
  // 
  goodSelectBtn = (id) => {
    // 
    let goods_id = {
      goods_id: id
    };
    _keyWayList(goods_id)
    .then(res=> {
      // console.log(res.data.data);
      if ( res.data.data === "" ) {
        this.setState({
          commodity1: true,                      //
        })
      } else {
        this.setState({
          commodity1: false,
          keyWayLists: res.data.data,         //
          defaultValues: res.data.data[0].id,
        })
      }
    })
    .catch(err => {
      console.log(err);
    })
    //  
    this.setState({
      goods_id: id,
    })
    let goodsLists = this.state.goodsLists;
    for (let i = 0; i < goodsLists.length; i++) {
      if ( goodsLists[i].id === id ) {
        this.setState({
          newGoodsLists: [goodsLists[i]],
        })
      }
    }
    this.setState({
      visible: false,                     //
      commodity_div: true,                //
      addGoodsNum: false,
      fanganShow: true,                   //
    });
  }
  // 
  addGoodsBtn = () => {
    this.setState({
      addGoodsNum: true,                     //addGoodsNum 
    })
  }
  // 
  compileGoods = () => {
    this.setState({
      addGoodsNum: true,                     //addGoodsNum 
    })
  }
  addGoodsNumShow = () => {
    this.setState({
      addGoodsNum: false,
    })
  }
  // 
  hiddenFangan = (commodity1,keyWayLists) => {//keyWayLists
    if ( commodity1 ) {
      this.setState({
        commodity1: false,              ////false  
        keyWayLists: keyWayLists,
      })
    } else {
      this.setState({
        commodity1: true,              ////false  
      })
    }
  }
  // 
  showCommodity1 = () => {
    if ( this.state.commodity_div ) {
      this.setState({
        commodity1: true,
      })
    } else {
      message.warning('');
    }
  }
  // id 
  handleValues = (value) => {//value
    // console.log(value);
    this.setState({
      defaultValues: value,
    })
  }
  //
  handleOneSubmit = () => {
    // console.log(this.state.defaultValues);
    let defaultValues = {
      id: this.state.defaultValues
    };
    // 
    _PublishTBSearch(defaultValues)
    .then(res => {
      // console.log(res.data);
      if ( res.data.code === 200 ) {
        this.onTabs()
        this.setState({
          firstDatas: res.data.data,
          activeKey: '2',
        })
      } else {
        message.error(res.data.msg)
      }
    })
    .catch(err => {
      console.log(err);
    })
  }

  render() {
    const { fanganShow,firstDatas,activeKey,defaultValues,keyWayLists,Ecommerce_type,total, goods_id,recentlyShow, spell, addGoodsNum, commodity_div, commodity1, additionalReview, goodsLists,newGoodsLists,publish_type } = this.state;
    return(
      <div>
        {/*  */}
        <PageHeader />
        <div className="rawp">
          <div className="contents release">
            <div>
              {/*  */}
              <Breadcrumb className="mbx">
                <Breadcrumb.Item>
                  <Link to="/center_new">
                    <Icon type="home" />
                  </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                  <Link to="/center_new">
                    <Icon type="user" />
                    <span>Center</span>
                  </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                  Release a task
                </Breadcrumb.Item>
              </Breadcrumb>
            </div>
            {/*    */}
            {
              recentlyShow ?
                <div className="releasetop">
                  <div>
                    <Icon type="alert" />
                    <span className="releasetop_span"></span>
                    <span>15</span>
                  </div>
                  <Button></Button>
                </div>
              :
                ''
            }
            <h1 style={{ color: '#e96262' }}></h1>
            <Tabs onChange={this.onTabs} onTabClick={this.onTabClick} className="releaseHeader" activeKey={activeKey} defaultActiveKey="1">
              <TabPane tab={<span className="releaseSpan"></span>} key='1'>
                <h2>1.</h2>
                <RadioGroup className="releaseLabel" onChange={this.onSpell} value={spell}>
                  {
                    publish_type ?
                      Ecommerce_type.map((item, index) => {
                        return(
                          <Radio key={index} value={item.platform}>{item.platform_name}</Radio>
                        )
                      })
                    :
                    Ecommerce_type ?
                      Ecommerce_type.map((item, index) => {
                        return(
                          <Radio key={index} value={item.platform}>{item.platform_name}</Radio>
                        )
                      })
                    :
                    ''
                  }
                </RadioGroup>
                {/*   */}
                {
                  spell === 100 || spell === 101 ?
                    Ecommerce_type ?
                      <Type1 FudianBtn={this.FudianBtn} Ecommerce_type={Ecommerce_type[0].task_type_list} />
                    :
                      ''
                  :
                  Ecommerce_type ?
                    <Type2 Ecommerce_type={Ecommerce_type} />
                  :
                    ''
                }
                <h2>2.</h2>
                <Button className="goodsBtns" onClick={ this.xuanzeBtn }></Button>
                {/* //      */}
                {
                  additionalReview ?
                    ''
                  :
                  <Button className="goodsBtns" onClick={ this.addGoodsBtn }></Button>
                }
                {/* commodity_divtrue  */}
                {
                  commodity_div ?
                    newGoodsLists.map((item, index) => {
                      return(
                        <div key={item.id} className="addGoodComponents_style">
                          <div className="commodity_div">
                            <p>
                              <img src={item.icon_img} alt='typeTu'/>
                              <span>{item.store_name}</span>
                            </p>
                            <p style={{ cursor: 'pointer' }} onClick={this.compileGoods}></p>
                          </div>
                          <div className="commodity_div commodity_div1">
                            <div style={{ display: 'flex', alignItems:'center' }}>
                              <img style={{ width: '16%' }} src={item.phone_img1} alt='typeTu'/>
                              <p>{item.goods_name}</p>
                            </div>
                            <p>{item.pay_price}</p>
                            <p>{item.show_price}</p>
                            <p>{item.pay_nums}</p>
                            <p>{item.rules}</p>
                            {
                              item.free_post === 1?
                                <p></p>
                              :
                              <p></p>
                            }
                          </div>
                        </div>
                      )
                    })
                  :
                    ""
                }

                {/* addGoodsNumtrue  */}
                {
                  addGoodsNum ?
                    <div style={{ position: 'relative' }} className="addGoodComponents_style">
                      <Icon onClick={this.addGoodsNumShow} style={{ position: 'absolute', right: '0', fontSize: '20px', padding: '10px' }} type="close" />
                      <AddGoodComponent />
                    </div>
                  :
                    ""
                }
                {/* 3.guanjianchi */}
                {/* //    */}
                {
                  additionalReview ?
                    ''
                  :
                  <KeywordComponent fanganShow={fanganShow} handleValue={this.handleValues} hidden={this.hiddenFangan} key={Math.random()} defaultValues={defaultValues} commodity_div={commodity_div} keyWayLists={keyWayLists} commodity1={commodity1} goods_id={goods_id} />
                }
                {/*  */}
                {
                  !commodity1 ?
                    <Button onClick={this.showCommodity1} style={{ marginBottom: '20px', marginTop: '20px' }} type="danger"></Button>
                  :
                    ''
                }
                <div style={{ padding: '50px 0' }} className="releaseBtnok">
                  <Button></Button>
                  <Button onClick={this.handleOneSubmit} type="primary"></Button>
                </div>
              </TabPane>
              {/*  */}
              <TabPane tab={<span className="releaseSpan"></span>} key='2'>
                <AppreciationServe firstDatas={firstDatas} />
              </TabPane>
              {/*  */}
              <TabPane tab={<span className="releaseSpan"></span>} disabled key='3'>Tab 3</TabPane>
            </Tabs>
          </div>
        </div>

        {/*  */}
        <Modal
          title="Basic Modal"
          visible={this.state.visible}
          footer={null}
          onCancel={this.handleCancel}
          wrapClassName="modal_Width"
          style={{ width: '70%' }}
        >
          <div className="yajin_center_tab">
            <div style={{ width: '30%' }}>
              <span>dsfds</span>
              <Select style={{ width: '75%' }} placeholder="">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </div>
            <p style={{ width: '30%' }}>
              <span>itme</span>
              <Input style={{ width: '20%', marginRight: '5px' }} />
              <span></span>
              <Input style={{ width: '20%', margin: '0 5px' }} />
              <span></span>
            </p>
            <p style={{ width: '40%', marginRight: '0' }}>
              <Input placeholder="" style={{ marginRight: '15px' }} />
              <Button></Button>
            </p>
          </div>
          {/* goods */}
          <div>
            <div className="goodsKu">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
            {/* div */}
            {
              goodsLists ?
                goodsLists.map((item, index) => {
                  return(
                    <div key={index} className="goodsKu_child">
                      <p>
                        <img src={item.icon_img} alt="types"/>
                        <span>{item.store_name}</span>
                      </p>
                      <div className="goodsKu_bottom">
                        <img src={item.phone_img1} alt="" />
                        <span>{item.goods_name}</span>
                        <span>{item.pay_price}</span>
                        <span>{item.rules}</span>
                        <span>{item.use_last_time}</span>
                        <Button onClick={()=>this.goodSelectBtn(item.id) } type="primary"></Button>
                      </div>
                    </div>
                  )
                })
              :
              ''
            }
            <Pagination className="paginations" defaultPageSize={3} showQuickJumper defaultCurrent={1} total={total} onChange={this.onPagination} />
          </div>
        </Modal>
      </div>
    )
  }
}

export default ReleaseTask
