import React, { Component } from 'react';
import { Icon, Select, Form, Button, Input, Upload, Modal, Radio, Checkbox, Row, Col, message } from 'antd';
import { Link } from 'react-router-dom';
import { _getnfo, _getStoreList } from '../../../component/api';                        //ajax


import './addGoods.css';                                                        //

const Option = Select.Option;                                                   //
const RadioGroup = Radio.Group;                                                 //

class addGoodComponents extends Component {
  constructor() {
    super();
    this.state = {
      previewVisible: false,                                                    //
      previewImage: '',
      fileList: [],                                                             //
      value1: 1,                                                                //
      value2: 1,
      mobileImg: '',                                                   // 
    }
  }

  componentWillMount() {
    let platform = {}
    _getStoreList(platform)
    .then(res=> {
      // console.log(res.data.data);
      this.setState({
        storeList: res.data.data.list,                     //
      })
    })
    .catch(err=> {
      console.log(err);
    })
  }

  // xuanze shops
  handleChange = (value) => {
    console.log(`selected ${value}`);
  }

  // 
  handleCancel = () => this.setState({ previewVisible: false })
  // 
  handlePreview = (file) => {
    console.log(file);
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    });
  }
  // 
  handleUpload = ({fileList}) => {
    console.log(fileList);
    this.setState({
      fileList,
    })
    if ( fileList.length === 0 ) {
      this.setState({
        mobileImg: '',
      })
    } else if ( fileList.length === 1 ) {
      this.setState({
        mobileImg: '',
      })
    } else {
      this.setState({
        mobileImg: '',
      })
    }
  }
  // 
  getNfo = () => {
    if ( this.state.shopURL ) {
      let url = {
        url: this.state.shopURL,
      }
      _getnfo(url)
      .then(res => {
          console.log(res.data.data);
          // form
          this.props.form.setFieldsValue({
            goods_name: res.data.data.goods_name,
          })
          if (this.state.fileList) {          //true  Mobile
            this.setState({
              port: true,
            })
          }
          // 
          this.setState({
             fileList: [
               {
                 url:res.data.data.img1,
                 uid: '1',
               },
               {
                 url:res.data.data.img2,
                 uid: '2',
               }
             ],
          })
      })
      .catch(err => {
        console.log(err);
      })
    } else {
      message.error("")
    }
  }
  onShopURL = (e) => {  //
    // console.log(e.target.value);
    this.setState({
      shopURL: e.target.value,
    })
  }

  // 
  radioBtn1 = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      value1: e.target.value,
    });
  }
  radioBtn2 = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      value2: e.target.value,
    });
  }
  // 
  checkboxBtn = (e) => {
    console.log(e);
  }


  // from
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { previewVisible, previewImage, fileList, mobileImg, storeList } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">{mobileImg}</div>
      </div>
    );
    return(
      <div className="goodsCss">
        <h2>Goods message </h2>
        <Form className="addGoods_form" onSubmit={this.handleSubmit}>
          <Form.Item
            label=""
            className="form_item"
          >
            {getFieldDecorator('note', {
              rules: [{ required: true, message: '!' }],
            })(
              <div style={{ display: 'flex' }}>
                <Input onChange={this.onShopURL} placeholder="" />
                <Button onClick={this.getNfo} style={{ marginLeft: '20px' }} type="primary">getGoods xinxi</Button>
              </div>
            )}
          </Form.Item>
          <div>
            <Form.Item
              label=""
              style={{ display: 'flex' }}
            >
              {getFieldDecorator('gender', {
                rules: [{ required: true, message: '!' }],
              })(

                <Select placeholder="" style={{ width: 250, marginRight:'10px' }} onChange={this.handleChange}>
                  {
                    storeList ?
                      storeList.map((item, index) => {
                        return(
                          <Option key={index} value={item.id}>{item.store_name}</Option>
                        )
                      })
                    :
                    <Option value=""></Option>
                  }
                </Select>
              )}
            </Form.Item>
            <Link style={{ paddingTop: '9px' }} to="/bind/bindShops">+ bingShops</Link>
          </div>
          <Form.Item
            label=""
            className="form_item"
          >
            {getFieldDecorator('goods_name', {
              rules: [{ required: true, message: '!' }],
            })(
              <Input placeholder="" />
            )}
          </Form.Item>
          <Form.Item label="">
            <div className="uploads">
              <Upload
                action="//jsonplaceholder.typicode.com/posts/"
                listType="picture-card"
                fileList={fileList}
                onPreview={this.handlePreview}
                onChange={this.handleUpload}
              >
                {fileList.length >= 3 ? null : uploadButton}
              </Upload>
              <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                <img alt="example" style={{ width: '100%' }} src={previewImage} />
              </Modal>
            </div>
          </Form.Item>
          {/* <Form.Item
            label="Baby name"
            >
            {getFieldDecorator('images', {
              rules: [{ required: false, message: '!' }],
            })(
              <div>
            {
            fileList ?
            fileList.map((item, index) => {
            return(
            <img style={{ width: '20%', paddingRight: '10px' }} key={index} src={item.url} alt="goodstu"/>
            )
            })
            :
            ''
            }
            <ImagePicker
            length={3}
            files={fileList}
            onChange={this.handleUpload}
            onImageClick={(index, fs) => console.log(index, fs)}
            selectable={fileList.length < 3}
            />
              </div>
            )}
          </Form.Item> */}
          {/*  */}
          <Form.Item label="" className="form_item" style={{ display: 'flex', alignItems: 'center' }}>
            <RadioGroup className="radio_child" onChange={this.radioBtn1} value={this.state.value1}>
              <Radio value={1}></Radio>
              <Radio value={2}>
                <Input className="radio_input" placeholder="" />
                <Input className="radio_input" placeholder="" />
              </Radio>
            </RadioGroup>
          </Form.Item>
          {/*  */}
          <Form.Item label="" className="form_item">
            {getFieldDecorator('moneys', {
              rules: [{ required: true, message: 'Please input your babyName!' }],
            })(
              <div className="xaidanMoney">
                <p>
                  <Input className="xaidanMoney_input" placeholder="size" />
                  <span> </span>
                </p>
                <p>
                  <span></span>
                  <Input className="xaidanMoney_input" />
                  <span>  </span>
                  <span className="xaidanMoney_span"></span>
                </p>
              </div>
            )}
          </Form.Item>
          {/* expressage */}
          <Form.Item label="" className="form_item">
            {getFieldDecorator('moneys', {
              rules: [{ required: true, message: 'Please input your babyName!' }],
            })(
              <div className="xaidanMoney">
                <p>
                  <Input className="xaidanMoney_input" placeholder="size" />
                  <span className="xaidanMoney_span"></span>
                </p>
              </div>
            )}
          </Form.Item>
          {/* expressage  */}
          <Form.Item label="" className="form_item" style={{ display: 'flex', alignItems: 'center' }}>
            <RadioGroup className="radio_child" onChange={this.radioBtn2} value={this.state.value2}>
              <Radio value={1}></Radio>
              <Radio value={2}>
                <span></span>
                <span className="xaidanMoney_span">10</span>
              </Radio>
            </RadioGroup>
          </Form.Item>
          {/* card  */}
          <Form.Item label="/" className="form_item" style={{ display: 'flex', alignItems: 'center', height: '30px' }}>
            <Checkbox.Group style={{ width: '100%' }} onChange={this.checkboxBtn}>
              <Row style={{ paddingTop: '10px' }}>
                <Col span={6}><Checkbox value="A"></Checkbox></Col>
                <Col span={6}><Checkbox value="B">
                  <span>/</span>
                  {/* <span className="xaidanMoney_span"></span> */}
                </Checkbox></Col>
                <span className="xaidanMoney_span"></span>
              </Row>
            </Checkbox.Group>
          </Form.Item>
          <p className="button_p"></p>
          {/*  */}
          <Form.Item className="submit_btn">
            <Button className="bottom_btns" type="danger" ghost></Button>
            <Button className="bottom_btns" type="primary" htmlType="submit"></Button>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

const addGoodComponent = Form.create()(addGoodComponents);
export default addGoodComponent
