  onChangeTask = (e) => {
    if ( e.target.checked ) {
      this.setState({
        dateShow: true,
      })
    } else {
      this.setState({
        dateShow: false,
      })
    }
  }
  // 
  handleOpenChange = (open) => {
    if (open) {
      this.setState({ mode: 'date' });
    }
  }
  handlePanelChange = (value, mode) => {
    this.setState({ mode });
  }
  handleOk = (e) => {//
    console.log(e._d);
  }
  handleChange = (e) => {
    console.log(e._d);
  }
  // 
  onInputNumber = (type,value) => {
    console.log(type,value);
    const newObject = Object.assign({}, this.state.object, {
      [type]: value
    });
    var getSum=function(ar){
    var arr=ar
    var sum=arr.reduce(function(prev, curr, idx, arr){
       return prev + curr;
       })
     return  sum;
    }
    console.log(newObject);
    console.log(getSum(Object.values(newObject)));
    this.setState({
      object: newObject,
      aggregateNum: getSum(Object.values(newObject))
    })
  }

  // 
  addPlanBtn = () => {
    nums.push(1)
    this.setState({
      nums: nums,
    })
    // this.props.form.setFieldsValue({
    //   bill_2: 1,
    // })
  }
  // 
  deleteBtn = (index) => {
    let as = this.state.nums;
    as.splice(index,1)
    this.setState({
      nums: as
    })
  }

  // 
  onExpressage = (e) => {
    console.log('', e.target.value);
    this.setState({
      value: e.target.value,
    });
  }
  // 
  // 
  onAccomplish = (e) => {
    console.log(` = ${e.target.checked}`);
    if ( e.target.checked ) {
      this.setState({
        speedNum: 1,
      })
    } else {
      this.setState({
        speedNum: '',
      })
    }
  }//
  onAccomplishChild = (e) => {
    console.log('', e.target.value);
    this.setState({
      speedNum: e.target.value,
    });
  }
