  addGraphic = () => {
    graphicNum.push(1)
    this.setState({
      graphicNum: graphicNum,
    })
  }// 
  delGraphic = (index) => {
    let delG = this.state.graphicNum;
    delG.splice(index,1)
    this.setState({
      graphicNum: delG
    })
  }
