  onGoodreputation1 = (checkedValues) => {
    // console.log('checked = ', checkedValues);
    if ( checkedValues.indexOf('add_to_merchants_eva') === -1 ) {
      this.setState({//
        show4: false,
      })
    } else {
      this.setState({//
        show4: true,
      })
    }
    if ( checkedValues.indexOf('add_to_text_pic_eva') === -1 ) {
      this.setState({//
        show5: false,
      })
    } else {
      this.setState({//
        show5: true,
      })
    }
  }
